<template>
  <svg id="BirdClock" ref="clock"></svg>
</template>

<script>
const TIME_STEPS = 48;

import * as d3 from "d3";

export default {
  name: "BirdClock",
  props: {
    activeBirds: Object,
    allBirds: Array,
    colorScale: Function,
    offset: Number,
    hovering: Number,
  },
  data() {
    return {
      width: 928,
      height: 928,
      margin: 10,
      innerRadius: 150,
      bird_groups: null,
      fontSize: window.innerWidth <= 800 ? 14 : 12,
    };
  },
  computed: {
    outerRadius() {
      return this.width / 2 - this.margin;
    },
    x() {
      return d3
        .scaleLinear()
        .domain([0, TIME_STEPS])
        .range([Math.PI, 3 * Math.PI]);
    },
    y() {
      return d3
        .scaleRadial()
        .domain([0, this.maxValue])
        .range([this.innerRadius, this.outerRadius]);
    },
    maxValue() {
      return Math.max(
        40,
        0.97 *
          this.toPlot[this.toPlot.length - 1].high.reduce((a, b) =>
            b > a ? b : a
          )
      );
    },
    toPlot() {
      let stack = (a) => {
        var stacked = [];
        var low = [];
        var high = [];
        for (var b = 0; b < a.length; b++) {
          for (var i = 0; i < TIME_STEPS; i++) {
            low[i] = high[i] || 0;
            high[i] = low[i] + (this.activeBirds[b] ? a[b][i] : 0);
          }
          stacked.push({
            name: a[b]["Com_Name"],
            low: low.map((d) => d),
            high: high.map((d) => d),
            height: Object.values(a[b]).slice(0, TIME_STEPS),
            //   : Array(TIME_STEPS).fill(0),
          });
        }
        return stacked;
      };

      return stack(this.allBirds);
    },
  },
  methods: {
    argMax(array) {
      return array
        .map((x, i) => [x, i])
        .reduce((r, a) => (a[0] > r[0] ? a : r))[1];
    },
    getLabelYOffset(d) {
      if (d.name == "European Robin") {
        return -10;
      } else if (d.name == "Short-toed Treecreeper") {
        return 11;
      } else if (d.name == "Goldcrest") {
        return -2;
      } else if (d.name == "Eurasian Blackbird") {
        return 11;
      } else if (d.name == "Hawfinch") {
        return 6;
      } else if (d.name == "Great Spotted Woodpecker") {
        return -13;
      } else {
        return 1;
      }
    },
    getLabelPosition(d) {
      if (d.name == "Eurasian Blackbird") {
        return 16;
      } else if (d.name == "Eurasian Wren") {
        return 18;
      } else if (d.name == "Redwing") {
        return 27;
      } else if (d.name == "Hawfinch") {
        return 26;
      } else if (d.name == "Tawny Owl") {
        return 5;
      } else {
        return this.argMax(d.height);
      }
    },
    getLabelWidth(d) {
      let width = d.name.length * 5.8 + 15;
      return (width * this.fontSize) / 12;
    },
    getLabelHeight() {
      return this.fontSize + 3;
    },
  },
  watch: {
    hovering(val) {
      if (val !== null) {
        d3.selectAll(".birdpath").style("fill-opacity", (d, i) => {
          return val == i ? 1 : 0.5;
        });
        d3.selectAll(".textlabel").style("fill-opacity", (d, i) => {
          return val == i ? 1 : 0.2;
        });
        d3.selectAll(".textrect").style("fill-opacity", (d, i) => {
          return val == i ? 1 : 0.5;
        });
      } else {
        d3.selectAll(".birdpath").style("fill-opacity", 1);
        d3.selectAll(".textlabel").style("fill-opacity", 1);
        d3.selectAll(".textrect").style("fill-opacity", 1);
      }
    },
    toPlot(val) {
      const TRANSITION_DURATION = 600;

      const area = d3
        .areaRadial()
        .curve(d3.curveCatmullRomClosed)
        .angle((d, i) => this.x(i));

      this.bird_groups
        .data(val)
        .join("g")
        .attr("id", (d) => d.name);

      d3.selectAll(".birdpath")
        .data(val)
        .join(".birdpath")
        .transition()
        .ease(d3.easeCubicOut)
        .duration(TRANSITION_DURATION)
        .attr("d", (d) => {
          return area
            .innerRadius((v) => this.y(v[0]))
            .outerRadius((v) => this.y(v[1]))(d3.transpose([d.low, d.high]));
        });

      d3.selectAll(".textrect")
        .data(val)
        .join(".textrect")
        .transition()
        .ease(d3.easeCubicOut)
        .duration(TRANSITION_DURATION)
        .attr(
          "x",
          (d) =>
            this.y(
              d.low[this.getLabelPosition(d)] * 0.7 +
                d.high[this.getLabelPosition(d)] * 0.3
            ) *
              Math.sin(this.x(this.getLabelPosition(d))) -
            this.getLabelWidth(d) / 2
        )
        .attr(
          "y",
          (d) =>
            -this.y(
              d.low[this.getLabelPosition(d)] * 0.7 +
                d.high[this.getLabelPosition(d)] * 0.3
            ) *
              Math.cos(this.x(this.getLabelPosition(d))) -
            this.getLabelHeight(d) +
            3 +
            this.getLabelYOffset(d)
        )
        .style("opacity", (d, i) => (this.activeBirds[i] ? 0.5 : 0))
        .style("pointer-events", (d, i) =>
          this.activeBirds[i] ? "auto" : "none"
        );

      d3.selectAll(".textlabel")
        .data(val)
        .join(".textlabel")
        .transition()
        .ease(d3.easeCubicOut)
        .duration(TRANSITION_DURATION)
        .attr(
          "x",
          (d) =>
            this.y(
              d.low[this.getLabelPosition(d)] * 0.7 +
                d.high[this.getLabelPosition(d)] * 0.3
            ) * Math.sin(this.x(this.getLabelPosition(d)))
        )
        .attr(
          "y",
          (d) =>
            -this.y(
              d.low[this.getLabelPosition(d)] * 0.7 +
                d.high[this.getLabelPosition(d)] * 0.3
            ) *
              Math.cos(this.x(this.getLabelPosition(d))) +
            this.getLabelYOffset(d)
        )
        .style("opacity", (d, i) => (this.activeBirds[i] ? 1.0 : 0.0))
        .style("pointer-events", (d, i) =>
          this.activeBirds[i] ? "auto" : "none"
        );
      d3.selectAll(".grid")
        .transition()
        .duration(TRANSITION_DURATION)
        .ease(d3.easeCubicOut)
        .attr("r", (d) => this.y(d));

      d3.selectAll(".gridtext")
        .transition()
        .duration(TRANSITION_DURATION)
        .ease(d3.easeCubicOut)
        .attr("y", (d) => this.y(d));
    },
    offset(val, oldVal) {
      let duration = oldVal == 0 ? 600 : (val - oldVal) * 300;
      if (val !== 0) {
        d3.select("#timeline")
          .transition()
          .duration(duration)
          .ease(d3.easeCubicInOut)
          .attr("transform", `rotate(${(360 * (val - 12)) / 48})`)
          .style("opacity", 1);
      } else {
        d3.select("#timeline")
          .transition()
          .duration(duration)
          .ease(d3.easeCubicInOut)
          .style("opacity", 0);
      }
    },
  },
  mounted() {
    const area = d3
      .areaRadial()
      .curve(d3.curveCatmullRomClosed)
      .angle((d, i) => this.x(i));

    const svg = d3
      .select(this.$refs.clock)
      .attr("width", this.width)
      .attr("height", this.height)
      .attr("viewBox", [
        -this.width / 2,
        -this.height / 2,
        this.width,
        this.height,
      ])
      .attr("style", "width: 100%; height: auto; font: 10px sans-serif;")
      .attr("stroke-linejoin", "round")
      .attr("stroke-linecap", "round");

    this.bird_groups = svg
      .append("g")
      .attr("id", "bird_paths")
      .selectAll("g")
      .data(this.toPlot)
      .attr("id", (d) => d.name)
      .join("g");

    // var bird_group_paths =
    this.bird_groups
      .append("path")
      .attr("class", "birdpath")
      .attr("id", (d) => "birdpath" + d.name)
      .attr("fill", (d) => this.colorScale(d.name))
      .attr("stroke-width", 1.5)
      .attr("d", (d) => {
        return area
          .innerRadius((v) => this.y(v[0]))
          .outerRadius((v) => this.y(v[1]))(d3.transpose([d.low, d.high]));
      })
      .on("click", (e, d) => {
        this.$emit("toggleSolo", d);
      })
      .on("mouseenter", (e, d) => {
        this.$emit("mouseEnter", d);
      })
      .on("mouseleave", (e, d) => {
        this.$emit("mouseLeave", d);
      });

    const background = svg
      .append("g")
      .attr("id", "background")
      .style("opacity", 0.9);

    background
      .append("image")
      .attr("href", "sunset.png")
      .attr("x", -this.innerRadius)
      .attr("y", -this.innerRadius)
      .attr("width", this.innerRadius * 2)
      .attr("height", this.innerRadius * 2);

    var text_groups = svg
      .append("g")
      .attr("id", "bird_labels")
      .selectAll("g")
      .data(this.toPlot)
      .join("g")
      .attr("id", (d) => "label" + d.name);

    text_groups
      .append("rect")
      .attr("class", "textrect")
      .attr(
        "x",
        (d) =>
          this.y(
            d.low[this.getLabelPosition(d)] * 0.7 +
              d.high[this.getLabelPosition(d)] * 0.3
          ) *
            Math.sin(this.x(this.getLabelPosition(d))) -
          this.getLabelWidth(d) / 2
      )
      .attr(
        "y",
        (d) =>
          -this.y(
            d.low[this.getLabelPosition(d)] * 0.7 +
              d.high[this.getLabelPosition(d)] * 0.3
          ) *
            Math.cos(this.x(this.getLabelPosition(d))) -
          this.getLabelHeight(d) +
          3 +
          this.getLabelYOffset(d)
      )
      .attr("width", (d) => this.getLabelWidth(d))
      .attr("height", (d) => this.getLabelHeight(d))
      .attr("ry", 7)
      .attr("rx", 7)
      .style("fill", (d) => d3.color(this.colorScale(d.name)).brighter(1))
      .style("opacity", (d, i) => (this.activeBirds[i] ? 0.5 : 0))
      .style("pointer-events", (d, i) =>
        this.activeBirds[i] ? "auto" : "none"
      )
      .on("click", (e, d) => {
        this.$emit("toggleSolo", d);
      })
      .on("mouseenter", (e, d) => {
        this.$emit("mouseEnter", d);
      })
      .on("mouseleave", (e, d) => {
        this.$emit("mouseLeave", d);
      });

    // var text_group_labels =
    text_groups
      .append("text")
      .attr("class", "textlabel")
      .attr(
        "x",
        (d) =>
          this.y(
            d.low[this.getLabelPosition(d)] * 0.7 +
              d.high[this.getLabelPosition(d)] * 0.3
          ) * Math.sin(this.x(this.getLabelPosition(d)))
      )
      .attr(
        "y",
        (d) =>
          -this.y(
            d.low[this.getLabelPosition(d)] * 0.7 +
              d.high[this.getLabelPosition(d)] * 0.3
          ) *
            Math.cos(this.x(this.getLabelPosition(d))) +
          this.getLabelYOffset(d)
      )
      .text((d) => d.name)
      .attr("dy", "-1px")
      .attr("text-anchor", "middle")
      .attr("font-size", this.fontSize)
      .style("opacity", (d, i) => (this.activeBirds[i] ? 1.0 : 0.0))
      .style("pointer-events", (d, i) =>
        this.activeBirds[i] ? "auto" : "none"
      )
      .on("click", (e, d) => {
        this.$emit("toggleSolo", d);
      })
      .on("mouseenter", (e, d) => {
        this.$emit("mouseEnter", d);
      })
      .on("mouseleave", (e, d) => {
        this.$emit("mouseLeave", d);
      });

    const grid = svg.append("g").attr("class", "grid");

    var grids = [];

    grids[0] = 0;
    grids[1] = 39;
    grids[2] = 78;
    grids[3] = 156;

    for (var i = 2; i < 4; i += 1) {
      grids[i + 2] = i * 156;
    }

    grid
      .selectAll("circle")
      .data(grids)
      .enter()
      .append("circle")
      .attr("class", "grid")
      .attr("cx", 0)
      .attr("cy", 0)
      .attr("r", (d) => this.y(d))
      .style("fill", "none")
      .style("stroke", "black")
      .attr("stroke-opacity", 0.1);

    grid
      .append("g")
      .style("transform", "rotate(-45deg)")
      .selectAll("text")
      .data(grids)
      .enter()
      .append("text")
      .classed("gridtext", true)
      .attr("x", 0)
      .attr("y", (d) => this.y(d))
      .attr("dy", -2)
      .text((d, i) => {
        if (i == 1) {
          return "50%";
        } else if (i == 2) {
          return "100%";
        } else if (i == 3) {
          return "200%";
        } else if (i == 4) {
          return "400%";
        } else if (i == 5) {
          return "600%";
        }
      });

    svg
      .append("g")
      .classed("grid", true)
      .selectAll()
      .data([0, 4, 8, 12, 16, 20, 24, 28, 32, 36, 40, 44])
      .join("g")
      .call((g) =>
        g
          .append("path")
          .attr("stroke", "black")
          .attr("stroke-opacity", 0.1)
          .attr(
            "d",
            (d) => `
            M${d3.pointRadial(this.x(d), this.innerRadius)}
            L${d3.pointRadial(this.x(d), this.width * 1.42)}
          `
          )
      )
      .call((g) =>
        g
          .append("path")
          .attr("id", (d) => "text" + d)
          .datum((d) => [d, d - 13])
          .attr("fill", "none")
          .attr(
            "d",
            ([a]) => `M${d3.pointRadial(this.x(a), this.innerRadius)}
            L 0 0
        )}
          `
          )
      )
      .call((g) =>
        g
          .append("text")
          .attr("dy", -1)
          .style("fill", (d) => {
            if (d >= 16 && d <= 32) {
              return "black";
            } else {
              return "white";
            }
          })
          .append("textPath")
          .attr("startOffset", 6)
          .attr("xlink:href", (d) => "#text" + d)
          .text((d) => d / 2 + ":00")
      );

    var annos = svg.append("g").classed("grid", true);

    annos
      .append("path")
      .attr("id", "sunset")
      .datum([34, 21])
      .attr("fill", "none")
      .attr(
        "d",
        ([a]) => `
            M${d3.pointRadial(this.x(a), this.innerRadius)}
            L 0 0
        )}
          `
      );

    annos
      .append("text")
      .attr("dy", -1)
      .append("textPath")
      .attr("startOffset", 42)
      .attr("xlink:href", "#sunset")
      .style("font-weight", "bold")
      .text("Sunset");

    annos
      .append("path")
      .attr("id", "sunrise")
      .datum([16, 3])
      .attr("fill", "none")
      .attr(
        "d",
        ([a]) => `
            M${d3.pointRadial(this.x(a), this.innerRadius)}
            L 0 0
        )}
          `
      );

    // A${this.innerRadius},${this.innerRadius} 0,0,1 ${d3.pointRadial(
    //           this.x(b),
    //           this.innerRadius

    annos
      .append("text")
      .attr("dy", -1)
      .append("textPath")
      .attr("startOffset", 42)
      .attr("xlink:href", "#sunrise")
      .style("font-weight", "bold")
      .text("Sunrise");

    annos
      .append("line")
      .attr("id", "timeline")
      .attr("x1", -this.innerRadius - 1)
      .attr("y1", 0)
      .attr("x2", -478)
      .attr("y2", 0)
      .attr("stroke", "rgba(0,0,0,0.5)")
      .attr("stroke-width", "2px")
      .style("opacity", 0);
  },
};
</script>

<style>
#bird_paths path {
  transition: fill-opacity 0.3s;
}

#bird_paths:hover path {
  fill-opacity: 0.4;
  cursor: pointer;
}

#bird_paths path:hover {
  fill-opacity: 1;
}

#bird_paths,
#bird_labels {
  cursor: pointer;
}

.grid {
  pointer-events: none;
}

svg {
  user-select: none;
  transition: 1s transform;
}

image {
  pointer-events: none;
}
</style>
