<template>
  <div class="App">
    <h1>Bird signs and cycles, February, 2024</h1>
    <div class="intro">
      <p>
        Understanding the winter routines of my avian neighbors with the help of
        <a href="https://github.com/mcguirepr89/BirdNET-Pi">BirdNET-Pi</a>, a
        microphone, and the Cornell Lab of Ornithology's
        <a href="https://github.com/kahst/BirdNET-Analyzer"
          >BirdNET classifier</a
        >.
      </p>
      <p class="smallonly">Best viewed on larger screens.</p>
    </div>
    <div class="buttons" v-if="step >= 0">
      <div
        :class="{
          story: true,
          hiddenleft: i > step,
          hiddenright: i < step,
          opaque: i == 0 || i >= story.length - 3,
        }"
        v-for="(s, i) in story"
        :style="{
          left: s.left,
          top: s.top,
          right: s.right,
        }"
        :key="'story' + i"
      >
        <p v-html="s.text"></p>
        <div class="storybuttons">
          <button
            v-if="i < story.length - 1"
            style="display: inline"
            @click="
              step = -1;
              offset = 0;
              audioClip = -1;
            "
          >
            {{ step == 0 ? "Explore on your own" : "Close" }}
          </button>
          <button
            v-if="i < story.length - 1"
            style="display: inline"
            @click="nextStep"
          >
            {{ step == 0 ? "Start with the jackdaws" : "Next" }}
          </button>
          <button
            v-else
            style="display: inline"
            @click="
              step = -1;
              offset = 0;
              audioClip = -1;
            "
          >
            Explore the sounds and cycles of my avian neighbors.
          </button>
        </div>
      </div>
    </div>

    <div class="buttons">
      <BirdButton
        v-for="(bird, i) in birdCount"
        :url="bird['imageUrl']"
        :color="colorScale(bird['Com_Name'])"
        :key="bird['Com_Name']"
        :active="activeBirds[i]"
        :bird="bird"
        :index="i"
        @birdclick="buttonClick(i)"
        :hovering="hovering"
        :storyMode="step >= 0"
      />
    </div>

    <div class="viz">
      <div class="mute-wrapper">
        <div class="mute" @click="muted = !muted">
          <img src="buttons/unmute.png" v-if="!muted" />
          <img src="buttons/mute.png" v-else />
        </div>
        <div :class="{ label: true, activated: muted }">Muted</div>
      </div>
      <BirdClock
        :allBirds="birdCount"
        :activeBirds="activeBirds"
        :colorScale="colorScale"
        v-if="birdCount.length > 0"
        @toggleSolo="toggleSolo"
        @mouseEnter="mouseEnter"
        @mouseLeave="mouseLeave"
        :hovering="hovering"
        :offset="offset"
      />
    </div>

    <div class="footer">
      <p>By <a href="https://subject.space">Logan Williams</a>.</p>
      <p>
        All bird illustrations are by
        <a href="https://en.wikipedia.org/wiki/Francis_Orpen_Morris"
          >Francis Orpen Morris</a
        >, except for the Rose-ringed Parakeet, which is by
        <a href="https://en.wikipedia.org/wiki/Alexander_Francis_Lydon"
          >Alexander Francis Lydon</a
        >.
      </p>
    </div>
    <Sounds
      :bird="playBird"
      @soundStarted="playBird = null"
      :clip="audioClip"
      :muted="muted"
    />
  </div>
</template>

<script>
import BirdButton from "./components/BirdButton.vue";
import BirdClock from "./components/BirdClock.vue";
import Sounds from "./components/Sounds.vue";
import * as d3 from "d3";

const BIRD_COUNT = 25;

const order = [
  "Eurasian Jackdaw",
  "Carrion Crow",
  "Eurasian Magpie",
  "Eurasian Jay",

  "Tawny Owl",
  "Gray Heron",

  "Eurasian Moorhen",
  "Eurasian Coot",

  "Egyptian Goose",
  "Graylag Goose",
  "Greater White-fronted Goose",
  "Mallard",

  "Rose-ringed Parakeet",

  "Long-tailed Tit",
  "Great Tit",
  "Eurasian Blue Tit",

  "European Robin",
  "Eurasian Wren",
  "Short-toed Treecreeper",
  "Goldcrest",

  "Eurasian Blackbird",
  "Redwing",
  "Hawfinch",
  "Great Spotted Woodpecker",
];

export default {
  name: "App",
  data() {
    return {
      birdList: [],
      birdCount: [],
      activeBirds: Array(24).fill(true),
      step: 0,
      offset: 0,
      hovering: null,
      playBird: null,
      audioClip: -1,
      muted: false,
      colorScale: (name) => {
        let base = d3.scaleOrdinal(d3.schemeTableau10);
        for (let i = 0; i < 25; i++) {
          base(i);
        }

        const corvids = [
          "Eurasian Jackdaw",
          "Carrion Crow",

          "Eurasian Magpie",
          "Eurasian Jay",
        ];
        const fowl = [
          "Mallard",
          "Egyptian Goose",
          "Greater White-fronted Goose",
          "Graylag Goose",
        ];

        const rails = ["Eurasian Coot", "Eurasian Moorhen"];

        const chickadees = [
          "Eurasian Blue Tit",
          "Great Tit",
          "Long-tailed Tit",
        ];

        const misc_songbirds = [
          "European Robin",
          "Eurasian Wren",
          "Short-toed Treecreeper",
          "Goldcrest",
        ];

        const big_songbirds = ["Redwing", "Eurasian Blackbird", "Hawfinch"];

        if (corvids.indexOf(name) >= 0) {
          return d3.color(base(6)).brighter(corvids.indexOf(name) * 0.5 - 0.5);
        } else if (fowl.indexOf(name) >= 0) {
          return d3.color(base(0)).brighter(fowl.indexOf(name) * 0.5 - 0.5);
        } else if (rails.indexOf(name) >= 0) {
          return d3.color(base(1)).brighter(rails.indexOf(name));
        } else if (chickadees.indexOf(name) >= 0) {
          return d3
            .color(base(7))
            .brighter(chickadees.indexOf(name) * 0.5 - 0.5);
        } else if (misc_songbirds.indexOf(name) >= 0) {
          return d3
            .color(base(3))
            .brighter(misc_songbirds.indexOf(name) * 0.5 - 1);
        } else if (big_songbirds.indexOf(name) >= 0) {
          return d3
            .color(base(2))
            .brighter(big_songbirds.indexOf(name) * 0.5 - 0.5);
        } else if (name == "Rose-ringed Parakeet") {
          return d3.color(base(4)).brighter(0.5);
        } else if (name == "Tawny Owl") {
          return d3.color(base(8));
        } else if (name == "Gray Heron") {
          return d3.color(base(9));
        } else if (name == "Great Spotted Woodpecker") {
          return d3.color(base(5)).darker(0);
        } else {
          return d3.color(base(name));
        }
      },
      story: [
        {
          text: "How much of what we hear do we notice? A microphone on my balcony listened to the birds of Amsterdam, and suddenly, so did I.",
          birds: [],
          offset: 0,
          top: "6em",
        },
        {
          text: "The morning begins with the jackdaws. It is still dark when I awake, but I can tell it's around 6am. This winter the jackdaws have chosen a roost outside my bedroom window and their chatter is as reliable as any alarm.",
          birds: ["Eurasian Jackdaw"],
          offset: 12,
          playBird: "story",
          audioClip: 0,
          left: "12em",
          top: "6em",
        },
        {
          text: "Around 7:00, they are joined by the crows, stirred from their roosts and swooping among the bare, still-dark trees.",
          birds: ["Eurasian Jackdaw", "Carrion Crow"],
          offset: 14,
          playBird: "story",
          audioClip: 1,
        },
        {
          text: "The corvid dawn chorus wanes around 9:00 as the last of the stragglers depart for their morning commute to the coastline, soggy polders, and trash sorting facilities that surround the&nbsp;city.",
          birds: ["Eurasian Jackdaw", "Carrion Crow"],
          offset: 18,
          playBird: "story",
          audioClip: 2,
        },
        {
          text: "By midafternoon they return, jostling for position in the communal winter roosting trees, sounds of small negotiations, hellos, courtship and contentment echoing from branch to&nbsp;branch.",
          birds: ["Eurasian Jackdaw", "Carrion Crow"],
          offset: 30,
          playBird: "story",
          audioClip: 3,
        },
        {
          text: "Slowly, the thousands of chirpy caws diminish into the sleepy solitude of the&nbsp;night.",
          birds: ["Eurasian Jackdaw", "Carrion Crow"],
          offset: 40,
          playBird: "story",
          audioClip: 4,
        },
        {
          text: "But not so sleepy. Darkness is the domain of the tawny owl, the Vondelpark's charismatic nocturnal&nbsp;raptor.",
          birds: ["Eurasian Jackdaw", "Carrion Crow", "Tawny Owl"],
          offset: 50,
          playBird: "story",
          audioClip: 5,
        },
        {
          text: "Migratory geese fly laboriously by on midnight&nbsp;journeys.",
          birds: [
            "Eurasian Jackdaw",
            "Carrion Crow",
            "Tawny Owl",
            "Greater White-fronted Goose",
          ],
          // left: "45%",
          // offset: 50,
          left: "64%",
          right: "2em",
          top: "9em",
          playBird: "story",
          audioClip: 6,
        },
        {
          text: "And, of course, the coots and moorhens, never far out of sight but not often heard until their provincial pre-dawn&nbsp;squabbles.",
          birds: [
            "Eurasian Jackdaw",
            "Carrion Crow",
            "Tawny Owl",
            "Greater White-fronted Goose",
            "Eurasian Moorhen",
            "Eurasian Coot",
          ],
          left: "45%",
          offset: 54,
          playBird: "story",
          audioClip: 7,
        },
        {
          text: "Each species has its own routine. Day belongs to the parakeets, naturalized in the 1970s and now ubiquitous in the&nbsp;Randstad.",
          birds: ["Rose-ringed Parakeet"],
          top: "3em",
          right: "55%",
          left: "4em",
          offset: 0,
          playBird: "story",
          audioClip: 8,
        },
        {
          text: "And I particularly associate a certain type of lazy, cold winter sunrise with the aggrieved calls of the Egyptian&nbsp;Geese.",
          birds: ["Rose-ringed Parakeet", "Egyptian Goose", "Graylag Goose"],
          top: "17em",
          playBird: "story",
          audioClip: 9,
        },
        {
          text: "The short days all fall into their own predictable rhythm. Some songbirds begin their salutations promptly at&nbsp;sunrise.",
          birds: [
            "European Robin",
            "Eurasian Blackbird",
            "Eurasian Blue Tit",
            "Eurasian Wren",
            "Great Tit",
          ],
          right: "33%",
          top: "2em",
          playBird: "story",
          audioClip: 10,
        },
        {
          text: "While others seem to take longer to warm up in these dark&nbsp;days.",
          birds: [
            "European Robin",
            "Eurasian Blackbird",
            "Eurasian Blue Tit",
            "Eurasian Wren",
            "Great Tit",
            "Long-tailed Tit",
            "Short-toed Treecreeper",
            "Goldcrest",
          ],
          right: "46%",
          top: "2em",
          playBird: "story",
          audioClip: 11,
        },
        {
          text: "I'm excited to have a small window into these daily patterns and, eventually, their seasonality. As the days lengthen and breeding season begins in the spring, these patterns and these birds will&nbsp;change.",
          birds: [
            "European Robin",
            "Eurasian Blackbird",
            "Eurasian Blue Tit",
            "Eurasian Wren",
            "Great Tit",
            "Long-tailed Tit",
            "Short-toed Treecreeper",
            "Goldcrest",
          ],
          top: "6em",
        },
        {
          text: "In the dataset, a single songbird sings all night: the redwing. Is this real? I have seen redwings around this winter, but they are not especially common. How confidently can the AI model distinguish this nocturnal 'ziep' sound from other thrushes?",
          birds: [
            "European Robin",
            "Eurasian Blackbird",
            "Eurasian Blue Tit",
            "Eurasian Wren",
            "Great Tit",
            "Long-tailed Tit",
            "Short-toed Treecreeper",
            "Goldcrest",
            "Redwing",
          ],
          top: "6em",
          playBird: "story",
          audioClip: 12
        },
        {
          text: "Like the observation biases inherent in traditional birding, there are limitations everywhere here â€” from the AI model to the simple fact that not all birds are vocal. Despite this, I've found both to be valuable tools for training myself to look and listen a little deeper.",
          birds: [],
          top: "5em",
          playBird: null,
        },
      ],
      solo: null,
    };
  },
  components: {
    BirdButton,
    BirdClock,
    Sounds,
  },
  computed: {
    filteredActiveBirds() {
      return this.birdCount.filter((d, i) => this.activeBirds[i]);
    },
  },
  mounted() {
    d3.csv("bird_freq_count_30.csv").then((data) => {
      data = data.sort(
        (a, b) => order.indexOf(a.Com_Name) - order.indexOf(b.Com_Name)
      );

      this.birdCount = data
        .map((d) => {
          for (let i = 0; i < 48; i++) {
            d[i] = +d[i];
          }
          return d;
        })
        .map((d) => ({
          ...d,
          imageUrl: "birds/" + d.Com_Name.replaceAll(" ", "-") + ".png",
        }));
    });
  },
  methods: {
    nextStep() {
      this.step = (this.step + 1) % this.story.length;
      this.offset = this.story[this.step].offset ?? 0;
      this.playBird = this.story[this.step].playBird;
      this.audioClip = this.story[this.step].audioClip ?? -1;
      // set active birds
      if (this.story[this.step].birds.length > 0) {
        this.activeBirds = this.birdCount.map(
          (d) => this.story[this.step].birds.indexOf(d.Com_Name) >= 0
        );
      } else {
        this.activeBirds = Array(BIRD_COUNT).fill(true);
      }
    },
    toggleSolo(bird) {
      if (this.solo) {
        this.activeBirds = this.solo;
        this.solo = null;
        this.playBird = null;
      } else {
        this.solo = this.activeBirds;
        let index = this.birdCount.map((b) => b.Com_Name).indexOf(bird.name);
        this.activeBirds = this.activeBirds.map((d, i) => i == index);
        this.playBird = bird.name.replaceAll(" ", "-");
      }
    },
    mouseEnter(bird) {
      let index = this.birdCount.map((b) => b.Com_Name).indexOf(bird.name);
      this.hovering = index;
    },
    mouseLeave() {
      this.hovering = null;
    },
    buttonClick(i) {
      if (this.step == -1) {
        this.solo = null;
        this.activeBirds[i] = !this.activeBirds[i];
        this.playBird = this.birdCount[i].Com_Name.replaceAll(" ", "-");
      }
    },
  },
};
</script>

<style>
