  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  z-index: 50;
  top: 10em;
  left: 6em;
  right: 6em;
  width: auto;
  padding: 0.5em;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5), 1px 1px 5px rgba(255, 255, 255, 1);
  backdrop-filter: blur(3px);
  transform: rotate(0deg);
  transform-origin: 50% 800px;
  transition: transform 0.5s, transform-origin 0.5s, opacity 0.3s;
  text-align: center;
}

