  border: 1px solid #444;
  background-color: #f0e6a0;
  padding: 0.25em 1em;
  border-radius: 2px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  font-family: initial;
  color: black;
  font-family: "Times New Roman", Times, serif;
}

