  position: relative;
}

.mute {
  width: 100%;
  height: 100%;
  padding: 10px;
  cursor: pointer;
  overflow: hidden;
  border-radius: 50%;
  background-color: rgba(240, 230, 160, 0.5);
  /* box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); */
}

.mute-wrapper {
  position: absolute;
  right: calc(2em + 20px);
  top: calc(2em - 10px);
  width: 40px;
  height: 40px;
  box-sizing: border-box;
}

.mute:hover {
  background-color: rgba(240, 230, 160, 1);
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.3);
}

.mute img {
  width: 150%;
