}

.mute-wrapper .label {
  opacity: 0;
  position: absolute;
  bottom: -3.5em;
  left: 0em;
  width: 50px;
  text-align: center;
  color: black;
  transition: none;
}

/* CSS animation to fade out */
@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.mute-wrapper .label.activated {
  /* animate */
  animation-name: fadeout;
  animation-duration: 0.5s;
