<template>
  <div
    :class="{
      BirdButton: true,
      active: this.active,
      parakeet: this.url.indexOf('Parakeet') >= 0,
      hovering: this.index == this.hovering || hover,
      storyActive: this.storyMode,
    }"
    :style="{ zIndex: hover || this.index == this.hovering ? 30 : 30 - index }"
    @mouseenter="hover = true"
    @mouseleave="hover = false"
    @click="buttonClick"
  >
    <img
      :src="url"
      draggable="false"
      :style="{
        backgroundImage: 'url(' + url.replace('birds/', 'shadows/') + ')',
      }"
    />

    <div
      :class="{ label: true, dark: isDark(color) }"
      :style="{ backgroundColor: color }"
    >
      {{ bird.Com_Name }}
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "BirdButton",
  props: {
    url: String,
    active: null,
    color: Object,
    bird: Object,
    index: Number,
    hovering: Number,
    storyMode: Boolean,
  },
  data() {
    return {
      hover: false,
    };
  },
  methods: {
    isDark(color) {
      return d3.hsl(color).l > 0.7;
    },
    buttonClick(e) {
      e.stopPropagation();
      if (this.active) {
        this.hover = false;
      }
      this.$emit("birdclick", this.index);
    },
  },
};
</script>

<style lang="scss">
.BirdButton {
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  filter: saturate(40%) contrast(15%) brightness(165%);
  position: relative;
  cursor: pointer;
  transition: 0.3s z-index;
  border-radius: 12px;
}

@media (max-width: 1024px) {
  .BirdButton {
    width: calc(11.8vw - 1px);
    height: calc(11.8vw - 1px);
  }
}

@media (max-width: 600px) {
  .BirdButton {
    width: 15.5vw;
    height: 15.5vw;
  }
}

img {
  max-width: 100%;
  max-height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
}

.parakeet {
  img {
    transform: scale(1.2) translate(0, 11px);
  }
}

.label {
  opacity: 0;
  position: absolute;
  z-index: 10;
  bottom: -2.5em;
  padding: 5px;
  color: white;
  font-size: 12px;
  text-align: center;
  border-radius: 8px;
  transition: 0.3s opacity;
  pointer-events: none;
}

.label.dark {
  color: black;
}

.BirdButton img {
  transition: 0.3s transform;
}

.BirdButton {
  background-color: rgba(255, 255, 255, 0);
  box-shadow: 0 0 10px 10px rgba(255, 255, 255, 0);
  transition: 0.1s filter, 0.3s background-color, 0.3s box-shadow;

  &.active {
    filter: saturate(100%);
  }

  //   &:hover:not(.storyActive),
  &.hovering:not(.storyActive),
//   &:hover.storyActive.active,
  &.hovering.storyActive.active {
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0 0 10px 10px rgba(255, 255, 255, 0.5);
    filter: saturate(100%) contrast(100%) brightness(100%) !important;

    .label {
      opacity: 1;
    }

    img {
      transform: scale(1.5);
    }

    &.parakeet {
      img {
        transform: scale(1.7) translate(0, 15px);
      }
    }
  }
}
</style>
