  "Gray-Heron": ["Gray_Heron-100-2024-01-19-birdnet-04_44_01.mp3"],
  "Eurasian-Wren": ["Eurasian_Wren-93-2024-02-01-birdnet-09_58_55.mp3"],
  "Graylag-Goose": [
    "Graylag_Goose-81-2024-01-23-birdnet-08_45_02.mp3",
    "Graylag_Goose-87-2024-01-16-birdnet-10_44_50.mp3",
  ],
