sounds["story"] = [
  "jackdaw_morning.mp3",
  "crows.mp3",
  "jackdaw_straggler.mp3",
  "jackdaw_afternoon.mp3",
  "jackdaw_night.mp3",
  "tawny_owl.mp3",
  "white-fronted-goose.mp3",
  "night_coots.mp3",
  "parakeets_clipped.mp3",
  "egyptian_geese.mp3",
  "songbirds.mp3",
  "afternoon_songbirds.mp3",
  "redwing_clipped.mp3",
];

export default {
  name: "Sounds",
  props: {
    bird: null,
    muted: Boolean,
    clip: Number,
  },
  mounted() {
    // create audio elements
    for (const bird in sounds) {
      sounds[bird] = sounds[bird].map((sound) => {
        const audio = new Audio(`sounds/${bird}/${sound}`);
        audio.load();
        return audio;
      });
    }
  },
  methods: {
    play(bird) {
      let audioClip =
        this.clip >= 0
          ? this.clip
          : Math.floor(Math.random() * sounds[bird].length);

      const audio = sounds[bird][audioClip];

      if (!this.muted) {
        audio.volume = 1;
      } else {
        audio.volume = 0;
      }

      // play and emit event when done
      audio.play();
      audio.onended = () => {
        this.$emit("soundStarted");
      };
    },
  },
  watch: {
    bird(newBird) {
      if (newBird) {
        this.play(newBird);
      }
    },
    clip(newClip) {
      if (this.bird == "story") {
        for (const bird in sounds) {
          sounds[bird].forEach((audio, i) => {
            if (bird !== "story" || i !== newClip) {
              let interval;
              interval = window.setInterval(() => {
                let vol = audio.volume - 0.04;
                if (vol <= 0) {
                  audio.volume = 0;
                  audio.pause();
                  audio.currentTime = 0;
                  window.clearInterval(interval);
                } else {
                  audio.volume = vol;
                }
              }, 10);
            }
          });
        }
      }

      if (newClip >= 0) {
        this.play(this.bird);
      }
    },
    muted(newMuted) {
      for (const bird in sounds) {
        sounds[bird].forEach((audio) => {
          if (newMuted) {
            audio.volume = 0;
            // mute
            audio.muted = true;
          } else {
            audio.volume = 1;
            // unmute
            audio.muted = false;
          }
        });
      }
    },
  },
};
</script>
